














import Vue from "vue";
import CardEditorMode from "@/enums/CardEditorMode";
export default Vue.extend({
  name: "Card",
  props: {
    card: Object,
  },
  data: () => ({
    enums: {
      CardEditorMode,
    },
  }),
  methods: {
    openCard(card: any) {
      this.$store.commit("workstation/setCardEditorMode", {
        mode: this.enums.CardEditorMode.Edit,
        card: this.card,
      });
    },
  },
});
