
















import Vue from "vue";
import CardEditorMode from "@/enums/CardEditorMode";
export default Vue.extend({
  name: "Navbar",
  data: () => ({
    enums: {
      CardEditorMode,
    },
  }),
  methods: {
    openEditorToCreate() {
      this.$store.commit("workstation/setCardEditorMode", {
        mode: this.enums.CardEditorMode.Create,
      });
    },
    logOut(){
      this.$store.dispatch("logOut");
    }
  },
});
